@import url('https://fonts.googleapis.com/css2?family=Rubik+Iso&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Handlee&display=swap');

.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

h1 {
  margin-bottom: 30px;
  font-family: 'Rubik Iso', cursive;
  font-size: 50px;
}

.startBtn {
  --bg: #e74c3c;
  --text-color: #fff;
  position: relative;
  width: 150px;
  border: none;
  background: var(--bg);
  color: var(--text-color);
  padding: 1em;
  font-weight: bold;
  text-transform: uppercase;
  transition: 0.2s;
  border-radius: 5px;
  opacity: 0.8;
  letter-spacing: 1px;
  box-shadow: #c0392b 0px 7px 2px, #000 0px 8px 5px;
}

.startBtn:hover {
  opacity: 1;
}

.startBtn:active {
  top: 4px;
  box-shadow: #c0392b 0px 3px 2px,#000 0px 3px 5px;
}

img {
  height: 450px;
}

.Word {
  margin: 20px;
  font-size: 25pt;
  font-weight: bold;
  font-family: 'Handlee', cursive;
  height: 40px;
}

.alphabet {
  margin: 20px;
  width: 750px;
}

.alphabetBtn {
  margin: 8px;
  height: 40px;
  width: 40px;
  font-weight: bold;
  font-family: 'Handlee', cursive;
}

button {
  cursor: pointer;
}